export default {
	data() {
		return {
			denglu:false,
			globalinfo:{}
		}
	},
	methods: {
		
	},
	onShow() {
		let userinfo= uni.getStorageSync("globalinfo");
		if(userinfo){
			this.globalinfo = userinfo;
			this.denglu =true;		
		}else{
			this.denglu =false;
			this.globalinfo ={};
		}

	},
	onShareAppMessage() {
		return {
			title:"我的",
			path:"pages/me/me"
		}
	}
}
